import React from 'react';

export default class About extends React.Component {
    constructor(props) {
        super(props);

        this.header = "About me"
        this.text = "Something about me!"
        this.state = {
            component_text: this.text,
            component_header: this.header,
            open: false,
            toggleShow: this.props.toggleShow
        }
    }

    handleClick  = () => {
        this.props.toggleShow(true, this.text, this.header);
    }

    render() {
        return(
            <div className="content">
                <div className="col">
                <button className="btn btn-block" type="button" onClick={this.handleClick}>
                    <strong>About</strong>
                </button>
                </div>
            </div>
        )}
}