import React from 'react';

export default class Resume extends React.Component {
    constructor() {
        super();
        this.state = {
            open: false
        }
    }

    toggle() {
        this.setState({
            open: !this.state.open
        });
    }

    render() {
        return(
            <div className="content">
                <div className="col">
                <button className="btn btn-block" onClick={this.toggle.bind(this)}>
                    <strong>Resume</strong>

                </button>
                </div>
                { this.state.open ? 
                        <div>
                            <p>My Resume</p>
                            Resume
                        </div> : null}
            </div>
        )}
}